import React, { useContext, useState } from "react";
import { LabelContext } from "../labelDataContext";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";

import Tooltip from '@material-ui/core/Tooltip';
import Radio from "@material-ui/core/Radio";

import { Text, View } from 'react-native'

const PersonAbility2 = (props) => {

  const value = useContext(LabelContext);
  const [selectAbylity, setSelectAbylity] = React.useState(0);
  const btnDisbaled = selectAbylity == value.personClass.навыки.count;

  console.log(value.personClass.умения);
  const setId = (event) => {

    if (selectAbylity == value.personClass.навыки.count && event.target.checked) {
    } else {
      if (event.target.checked) {
        setSelectAbylity(selectAbylity + 1);
      } else {
        setSelectAbylity(selectAbylity - 1);
      }
      if (selectAbylity < 0) {
        setSelectAbylity(0);
      }

      event.preventDefault();

      value.setAbylityValue(event)
    }


  };


  return (
    <form style={{
      width: "100%",
      paddingLeft: 100,
      paddingRight: 100,
      paddingTop: 20,
      paddingDown: 10,

      alignContent: 'center'
    }}>
      <h4> Выберите классовые навыки персонажа</h4>
      <View
        style={{
          flexDirection: 'row',
          width: "100%",
          paddingLeft: 100,
          paddingRight: 100,
          paddingTop: 20,
          paddingDown: 10,

          alignContent: 'center'
        }}>
        {value.personClass.умения?.map((умение) => (
          <div>
            <Text>
              <b>{умение.name}</b>
              {'\n'}
              {умение.title}

            </Text>
            <FormGroup
              aria-label="weight"
              name="weight"
              className="form"

            >
              {умение.items?.map((i) =>
                  <FormControlLabel

                    control={<Radio />}
                    label={i.name}
                    value={i.name}
                    />
              )}
            </FormGroup>
          </div>
        ))}
      </View>
      <ButtonGroup
        variant="contained"
        color="primary"
        aria-label="text primary button group"
        style={{ marginTop: 15 }}
      >
        <Button onClick={() => value.prevPage()} style={{ margin: 25 }}>
          Предыдущий шаг
        </Button>
        <Button
          disabled={!btnDisbaled}
          onClick={() => value.nextPage()}
          style={{ margin: 25 }}
        >
          Следующий шаг
        </Button>
      </ButtonGroup>
    </form>
  );
};
export default PersonAbility2;
